.profile_edit {
    padding: 101px 0;
}
.profile_edit .container {
    max-width: 341px;
    width: 100%;
    margin: 0 auto;
}
.profile_edit_upload_picture {
    text-align: center;
    margin-bottom: 81px;
}
.profile_edit_upload_picture .picture_wrapper {
    width: 111px;
    height: 111px;
    border: 2px solid #202020;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}
.profile_edit_upload_picture .picture_wrapper img {
    width: 52px;
}
.profile_edit_upload_picture .upload_picture{
    font-size: 18px;
    font-weight: 500;
    line-height: 21px;
    letter-spacing: 0.1em;
    color: #1E1E1E;
    margin-top: 24px;
    background: transparent;
    border: 0;
    text-transform: uppercase;
}