.footer{
    border-top: 1px solid #202020;
    margin-top: 74px;
}
.footer_container{
    width: 929px;
    margin: 0 auto;
    padding-top: 59px;
    padding-bottom: 24px;
}
.footer_content{
    display: flex;
    flex: 0 0 50%;
}
.footer_logo{
    width: 50%;
}
.footer_logo a{
    width: 125px;
    position: relative;
}
.footer_logo a img{
    width: 125px;
}
.footer_navs{
    width: 45%;
    display: flex;
    justify-content: space-between;
}
.connects h3,
.contacts h3{
    font-weight: normal;
    font-size: 14px;
    line-height: 16.9px;
    color: #202020;
    text-transform: uppercase;
}
.connect_list a,
.contacts p{
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    text-decoration: none;
    color: #949494;
}
.connect_list a,
.contacts p{
    display: block;
    margin-top: 8px;
}
.connect_list a:first-child,
.contacts p:first-child{
    margin-top: 15px;
}
