.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

/* ==input field and label== */
.form_wrapper .input_group {
  margin-bottom: 24px;
}

.form_wrapper .input_field_label {
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  margin-bottom: 10px;
  display: block;
}

.form_wrapper .input_field, .form_wrapper .select_field {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  padding: 11px 16px;
  border: 1px solid #949494;
  display: block;
  width: 100%;
}

.form_wrapper .select_field {
  background-image: url(../src/icons/chevron-down.svg);
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: right 16px center;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.form_wrapper .select_field:focus-visible {
  outline: none;
}