/* custom chackbox */

input[type="checkbox"] {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }
  .checkbox {
    display: inline-block; 
    height: 18px;
    width: 18px;
    background: #fff;
    border: 2px #ddd solid;
    margin-right: 4px;
  }
  
  .checkbox--active {
    border-color: black;
    background: black;
  }

  .price_content button{
    background: none;
    border: none;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;
  }
  .price_content h3{
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
  }

