/* Paginations */

.pagination {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}
.pagination li{
    list-style: none;
}
.pagination .selected{
    background: #ddd;
}
.pagination a {
    color: black;
    float: left;
    padding: 10px 14px;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid #ddd;
    margin: 0 6px;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
}
  
  .pagination a.active {
    color: black;
  }
