.home_slider {
    height: 413px;
    margin-top: 46px;
    margin-bottom: 68px;
}

.home_slider .swiper {
    width: 100%;
    height: 100%;
    border: 2px solid #202020;
}
  
.home_slider .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
} 
.home_slider .swiper-pagination-bullet-active {
background: black;
}
.home_slider .swiper-horizontal>.swiper-pagination-bullets,.home_slider .swiper-pagination-bullets.swiper-pagination-horizontal,.home_slider .swiper-pagination-custom,.home_slider .swiper-pagination-fraction {
bottom: 27px;
}

.Latest_product{
    padding-top: 129px;
}

h2.section_heading {
    font-weight: bold;
    padding-bottom: 55px;
    font-size: 38px;
    line-height: 48px;
    text-align: center;
    color: #202020;
}
.testimonial_section{
    margin-top: 92px;
}
.tastimonial_slider{
    height: 356px;
}

.tastimonial_slider .swiper {
    width: 100%;
    height: 100%;
    border: 2px solid #202020;
}
.tastimonial_slider .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}
.swiper-button-next{
    background-image: url('../icons/Left-Arrow.svg');
}
 .swiper-button-prev {
    background-image: url('../icons/RightArrow.svg');

}
.swiper-button-next, .swiper-button-prev {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.tastimonial_slider .swiper-button-next:after,
.tastimonial_slider .swiper-rtl .swiper-button-prev:after {
    font-family: none;
    content: ' ';
}
.tastimonial_slider .swiper-button-prev:after,.tastimonial_slider .swiper-rtl .swiper-button-next:after {
    font-family: none;
    content: '';
}
.tastimonial_slider .swiper-button-prev, .tastimonial_slider .swiper-rtl .swiper-button-next {
    left: 75px;
    right: auto;
    cursor: pointer;
}
.tastimonial_slider .swiper-button-next, .tastimonial_slider .swiper-rtl .swiper-button-prev {
    right: 75px;
    left: auto;
    cursor: pointer;
}
.tastimonial_slider .swiper-horizontal>.swiper-pagination-bullets, .tastimonial_slider .swiper-pagination-bullets.swiper-pagination-horizontal, .tastimonial_slider .swiper-pagination-custom, .tastimonial_slider .swiper-pagination-fraction {
    bottom: 23px;
}
.testimonial_content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.testimonial_content p{
    max-width: 496px;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #949494;
}
.testimonial_content .user_img{
    margin-top: 20px;
    margin-bottom: 5px;
}
.testimonial_content h3{
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #202020;
}
.testimonial_content h4{
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    color: #949494;
}
.top_categories_section{
    margin-top: 98px;
}
.offer_section .Common_btn,
.home_section .Common_btn{
    margin: 0 auto;
    margin-top: 55px;
}
.home_section 
.top_Categories_content{
    display: inline-flex;
    flex-wrap: wrap;
    gap: 29px;
}
.category{
    width: 553px;
    border: 2px solid #202020;;
}
.category .category_img{
    width: 100%;
    height: 479px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.category .category_name h3{
    padding-bottom: 36px;
    text-align: center;
    font-weight: bold;
    font-size: 28px;
    line-height: 38px;
    color: #202020;
}